const stationCode = window.stationCode;
const stationName = window.stationName;
const stationAddress = window.stationAddress;
const stationPort = window.stationPort;
const stationStatus = window.stationStatus;

export const data = [];

for(let i = 0; i < stationCode.length; i++){
  const row_data = {
    stationNumber: stationCode[i],
    stationName: stationName[i],
    installAddress: stationAddress[i],
    portCount: stationPort[i],
    stationStatus: stationStatus[i],
  }
  data.push(row_data);
}

